const parseAndSymbolicateDevelopmentErrorStack = async ( error ) => {
  const parseErrorStack = require( 'react-native/Libraries/Core/Devtools/parseErrorStack' );
  const stack = parseErrorStack( error );

  console.log("Stack: ", stack);

  const symbolicateStackTrace = require( 'react-native/Libraries/Core/Devtools/symbolicateStackTrace' );
  const symbolicatedStackTrace = await symbolicateStackTrace( stack );
  console.log("Symbolicated: ", symbolicatedStackTrace);

  return symbolicatedStackTrace;
};

const parseAndSymbolicateProductionErrorStack = async ( error ) => {
  console.log("doing production error");
  console.log(error);

  if ( ! error || ! error.stack ) {
    return [];
  }

  return Array.isArray( error.stack ) ? error.stack : stackTraceParser.parse( error.stack );
};

const parseErrorStack = async ( error ) => {

  console.log(error);

  return parseAndSymbolicateProductionErrorStack( error );

  return// __DEV__ ?
    //parseAndSymbolicateDevelopmentErrorStack( error ) :
    parseAndSymbolicateProductionErrorStack( error );
};

ErrorUtils.setGlobalHandler( function( error ) {
  parseErrorStack( error )
    .then( ( result ) => {
      console.log( 'Error Stack: ', result );
    } )
    .catch( ( error ) => {
      console.error( 'Error while dealing with error: ', error );
    } )
    .finally( function() {
      alert( 'WE HAVE AN ERROR' );
    } );
} );

